@import 'styles.scss';

body, .x-files {
    overflow-x: hidden;
    width: 100%;
    height: 100%;
    padding: 0px;
    margin: 0px;
}

// Kinda hacky but this is for paragraphs of text on project pages
h6 {
    color: $grey;
    font-weight: 300;
    font-size: 20px;
    line-height: 40px;
    padding: 0;
    margin: 0;
    margin-bottom: 30px;
}

    .leader {
        margin-top: 20px;
    }
    
    .leader:first-letter {
        color: $orange;
        float: left;
        font-family: Georgia !important;
        font-size: 75px;
        line-height: 60px;
        padding-top: 10px;
        padding-right: 8px;
        padding-left: 3px;
    }

    h6 i {
        font-family: 'Lava Italic';
    }

    h6 strong {
        font-family: 'Lava Bold';
    }

    .italic {
        font-family: 'Lava Italic';
    }

    .indent {
        padding-left: 10%;
    }

    .inline {
        display: inline;
    }

    .back-button {
        font-family: $ss;
        font-weight: bold;
        font-size: 14px;
        background-color: rgb(230, 230, 230);
        padding: 5px 12px 5px 5px;
        border-radius: 3px;
        margin-left: 20px;
        cursor: pointer;
        text-transform: uppercase;
        display: none;
    }

    .glossary-term {
        margin-bottom: 15px;
    }

        .glossary-term > #g1, #g2, #g3 {
            color: rgb(168, 168, 168);
        }

    .explainer {
        padding-left: 10px;
        padding-bottom: 10px;
        opacity: .5;
        display: none;
        cursor: pointer;
    }

h4 {
    font-family: $ss;
    font-size: 25px;
    margin-bottom: 10px;
}

ol {
    list-style-type: square;
}

    .shop-list {
        font-family: $ss;
    }

a {
    cursor: pointer;
}

sup {
    font-weight: bold;
    opacity: .3;
    cursor: pointer;
}

sup:hover {
    opacity: 1;
    color: $orange;
}

.closed-indicator {
    font-size: 12px;
    padding-left: 5px;
    opacity: .7;
    color: red;
}

.med-photo {
    margin-top: 50px;
    margin-bottom: 30px;
    width: 120%;
    margin-left: -10%;
    max-width: 1100px;
}

    .med-photo p {
        font-size: 20px;
        color: rgb(172, 172, 172);
        margin-bottom: 20px;
        line-height: 30px;
    }

    .med-photo-img {
        background-image: url(../../images/projects/coffee/ben-balcony-coffee.jpg);
        background-position-y: 65% !important;
    }

.big-photo {
    margin-top: 50px;
    margin-bottom: 30px;
    max-width: 1200px;
    margin: auto;
}
  
    .big-photo p {
      font-size: 20px;
      color: rgb(172, 172, 172);
      margin-bottom: 20px;
      line-height: 30px;
    }

    .big-photo-img {
        background-image: url(../../images/projects/coffee/ben-balcony-coffee.jpg);
        background-position-y: 65% !important;
    }

    .big-emoji {
        font-size: 60px;
        margin-bottom: 0px;
    }

    .caption {
        width: 90%;
        margin: auto;
    }

        .caption p {
            font-size: 18px;
            font-family: $ss;
        }

.update-line {
    font-family: $ss;
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 600;
    padding-top: 10px;
    letter-spacing: .3px;
    opacity: .3;
}

.bg-orange {
    height: 250px;
    background-color: rgb(221, 221, 221);
    background-size: cover;
    background-position: center;
}

.orange-btn {
    margin: 30px 0px;
    background-color: $orange;
    color: white;
    padding: 10px 50px;
    border-radius: 4px;
    margin: auto;
    margin-top: 30px;
    margin-bottom: 30px;
}

iframe {
    width: 100%;
    height: 500px;
    margin-top: 20px;
    margin-bottom: 40px;
}

.quote {
    border-radius: 4px;
    background-color: rgb(245, 245, 245);
    width: 80%;
    max-width: 800px;
    padding: 10px 25px 25px 35px;
    margin: auto;
    margin-bottom: 35px;
    margin-top: 15px;
    border: 1px solid $grey;
    border-bottom: 5px solid $grey;
}

    .quote > h2 {
        font-size: 25px;
        font-style: italic;
        color: #333333;
        line-height: 35px;
    }

    .quote > p {
        font-size: 18px;
        font-weight: 600;
        color: grey;
    }

.rootProject {
    opacity: .5;
}

.project .intro {
    margin-top: 100px;
}

.project .intro h1 {
    font-weight: 600;
}

.project-card {
    position: relative;
    margin-top: 60px;
    margin-bottom: 60px;
}

    .project-card > p {
        width: 60%;
        opacity: .5;
    }

    .project-card > h2 {
        width: 60%;
    }

    .project-img {
        width: 250px;
        height: 100%;
        position: absolute;
        right: 0;
        top: 0;
        background-size: cover;
        background-position: center;
    }

    .project-card > img {
        width: 100px;
        margin-top: 20px;
        margin-bottom: -20px;
        background-color: transparent;
    }

// REVIEWS
.review h4 {
    font-size: 35px;
    font-weight: 300;
}

.review h4:first-of-type {
    margin-bottom: -10px;
}

.review a {
    font-family: $ss;
    font-size: 18px;
    line-height: 35px;
}

.review p {
    font-family: $ss;
    font-size: 18px;
    line-height: 35px;
}

.review .fill {
    height: 400px;
}

    .review .fill a img {
        border-radius: 3px;
        border: 1px solid rgb(168, 168, 168);
        width: 100%;
    }

.gallery-blackbox {
    margin-bottom: 20px;
    border-radius: 3px;
}

    .gallery-indicator {
        display: none;
    }

.gallery-container {
    margin: auto;
    height: 120px;
    margin-left: -10px;
}

    .gallery-container.flex-grid {
        margin-bottom: 0px;
    }

.gallery-photo {
    flex: 1;
    padding-left: 10px;
    position: relative;
}

    .gallery-photo a img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        border: 1px solid rgb(168, 168, 168);
    }

    .gallery-photo a img {
        width: 100%;
        height: 100%;
        background-color: rgb(243, 243, 243);
        object-fit: cover;
        border-radius: 3px;
        opacity: 1;
        transition: .3s ease all;
    }

        .gallery-photo a img:hover {
            opacity: .8;
        }

    .fill a img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .scorecard {
        width: 100%;
        margin-bottom: 50px;
    }

    .score-col {
        padding: 20px 0px 20px 30px;
        text-align: left;
        width: 46%;
        background-color: rgba(243, 243, 243, 0.603);
        border-radius: 3px;

    }

        .score-col p {
            font-size: 16px;
            line-height: 25px;
            padding-right: 20px;
            margin-bottom: -10px;
        }

        .score-col i {
            font-size: 18px;
            opacity: .4;
            width: 30px;
            padding-right: 10px;
            text-align: center;
            margin-left: -5px;
        }

        .score-col div {
            padding-left: 5px;
            margin-bottom: 12px;
        }

        .score-col hr {
            display: none;
        }

        .score-col > h2 {
            margin: 0px 0px 30px 0px;
        }

        .score-col span {
            display: inline-block;
        }

        .score-text {
            display: inline-block;
            max-width: 75%;
        }

        .score-emoji {
            padding-top: 2px;
            padding-right: 8px;
            display: inline-block;
            vertical-align: top;
        }

        .score-col .small-note {
            opacity: .7;
        }

.writeup {
    padding: 0px 30px 0px 30px;
}

    .writeup h2 {
        margin-bottom: 20px;
        padding-bottom: 8px;
    }

    .writeup img {
        width: 60px;
        height: 60px;
        background-position: center;
        background-size: contain;
        border-radius: 50%;
        margin-top: 15px;
        margin-bottom: 5px;
    }

    .writeup-guest {
        background-color: rgba(243, 243, 243, 0.603);
        padding: 20px 20px 5px 30px;
        margin-top: 35px;
        margin-bottom: 40px;
        border-radius: 5px;
    }
    
        .writeup-guest strong {
            font-family: $ss;
            font-size: 18px;
        }

        .writeup-guest h6 {
            font-family: $ss;
            font-size: 18px;
        }
.proj-button-container {
    width: 100%;
    margin: auto;
    text-align: center;
    padding-top: 15px;
    z-index: 10;
}

    .proj-button-container hr {
        margin-top: -18px;
        width: 120vw;
        margin-left: -25%;
        position: absolute;
        border-color: rgba(231, 231, 231, 0.37);
        z-index: 1;
    }

    .proj-button {
        padding: 10px 20px;
        border: 1px solid #333;
        font-weight: bold;
        border-radius: 3px;
        background-color: white;
        position: relative;
        z-index: 10;
        border-bottom: 4px solid #333;
    }

        .proj-button:hover {
            background-color: rgb(243, 243, 243);
        }

.img-link {
    cursor: pointer;
    display: inline-block;
}

@media screen and (max-width: 720px) {
    .project-img {
        display: none;
    }

    .project-card > p {
        width: 100%;
    }
}

@media screen and (max-width: 885px) {
    .gallery-blackbox {
        position: relative;
    }

        .gallery-indicator {
            position: absolute;
            display: block;
            bottom: 15px;
            right: 10px;
            background-color: rgba(12, 12, 12, 0.432);
            padding: 0px 10px;
            border-radius: 3px;
            cursor: pointer;
            transition: .3s ease;
        }

        .gallery-indicator:hover {
            background-color: rgb(12, 12, 12);
        }

            .gallery-indicator a {
                color: white;
            }

        .gallery-blackbox .fill {
            width: 125vw;
            margin-left: -20%;
            border-radius: none;
        }

    .gallery-container.flex-grid {
        display: none;
    }
    .gallery-photo {
        display: none;
    }

    .scorecard {
        margin-top: 30px;
    }

    .score-col {
        width: 100%;
        padding-right: 0px;
        padding-left: 0px;
        background-color: white;
        padding-top: 0px;
        padding-bottom: 0px;
        margin-bottom: 20px;
    }

        .score-text {
            max-width: 85%;
        }

        .score-col div {
            margin-bottom: 10px;
        }

    .writeup {
        padding: 0px 5px;
    }

    .recs {
        display: none;
    }
    
    .meta-emoji {
        display: none;
    }
}